import request from '@/utils/request'

export function getInventory(data) {
  return request({
    url: '/product/business/inventory/findByCondition',
    method: 'post',
    data
  })
}
export function addInventory(data) {
  return request({
    url: '/product/business/inventory/add',
    method: 'post',
    data
  })
}
export function getinventoryDraft(data) {
  //查询商品
  return request({
    url: '/product/business/inventoryDraft/findByCondition',
    method: 'post',
    data
  })
}
export function getCode(data) {
  //查询编号
  return request({
    url: '/product/business/inventoryDraft/genInventoryNo',
    method: 'post',
    data
  })
}
export function getDraft(data) {
  //添加草稿箱
  return request({
    url: '/product/business/inventoryDraft/add',
    method: 'post',
    data: data
  })
}
export function getProAdd(data) {
  //添加草稿箱
  return request({
    url: '/product/business/inventory/add',
    method: 'post',
    data: data
  })
}
export function getPdList(data) {
  //查询盘点列表
  return request({
    url: '/product/business/inventory/findByCondition',
    method: 'post',
    data: data
  })
}
export function getDetailList(data) {
  //详情
  return request({
    url: `/product/business/inventory/findById?id=${data.id}`,
    method: 'post',
    data
  })
}
export function del(data) {
  //详情
  return request({
    url: '/product/business/inventoryDraft/del',
    method: 'post',
    data
  })
}
export function getDraftDetail(data) {
  //草稿箱详情，继续盘点
  return request({
    url: `/product/business/inventoryDraft/findDraftById?id=${data.id}`,
    method: 'post',
    data
  })
}
export function getDraftLists(data) {
  //草稿箱列表
  return request({
    url: '/product/business/inventoryDraft/findDraftByCondition',
    method: 'post',
    data
  })
}

export function delectDetails(data) {
  //草稿箱删除记录
  return request({
    url: '/product/business/inventoryDraft/delDetail',
    method: 'post',
    data
  })
}

