<template>
  <div class="app-container">
    <!-- 筛选条件 -->
    <el-radio-group v-model="tips" @change="switchData">
      <el-radio-button label="1">盘点记录</el-radio-button>
      <el-radio-button label="2">草稿箱</el-radio-button>
    </el-radio-group>
    <el-card class="operate-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float:right" type="primary" @click="handleSearchList()" size="small">
          查询搜索
        </el-button>
        <el-button style="float:right;margin-right: 15px" @click="handleResetSearch()" size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="listQuery" size="small" label-width="120px">
          <el-form-item label="单据编号：">
            <el-input v-model="listQuery.orderNo" class="input-width" placeholder="订单编号"></el-input>
          </el-form-item>
          <el-form-item label="盘点日期：">
            <el-date-picker v-model="listQuery.invenetTime" type="date" value-format="yyyy-MM-dd" placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card class="operate-container" style="margin-top:10px" shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
      <span class="tips" v-if="tips==1">温馨提示：盘点单盘盈后会自动入库，盘亏后会自动出库；</span>
      <span class="tips" v-if="tips==2">温馨提示：盘点时临时保存的盘点单，会进入草稿箱，可在草稿箱找到该盘点单，继续盘点；</span>
      <el-button style="float:right;margin-right: 15px" @click="addCom()" size="small" type="primary">
        新增盘点
      </el-button>
    </el-card>
    <el-card class="operate-container" style="margin-top:10px">
      <el-table :data="tableData" style="width: 100%" border>
        <el-table-column label="序号" type="index" width="50" align="center">
        </el-table-column>
        <el-table-column label="单据编号" width="200" show-overflow-tooltip prop="name" align="center">
          <template slot-scope="scope">
            {{scope.row.inventoryNo}}
          </template>
        </el-table-column>
        <el-table-column label="盘点日期"
                         width="200"
                         min-width="20%"
                         prop="createTime"
                         align="center">
          <template slot-scope="scope">
            {{scope.row.createTime}}
          </template>
        </el-table-column>
        <el-table-column label="盘点人"
                         min-width="15%"
                         prop="userName"
                         align="center">
        </el-table-column>
        <el-table-column label="盘点商品条数"
                         min-width="15%"
                         prop="productCount"
                         align="center">
        </el-table-column>
        <el-table-column label="盘盈数量"                     
                         min-width="15%"
                         v-if="tips==1"
                         prop="profitCount"
                         align="center">
        </el-table-column>
        <el-table-column label="盘亏数量"
                         v-if="tips==1"                     
                         min-width="15%"
                         prop="lossCount"
                         align="center">
        </el-table-column>
        <el-table-column label="商品种类"
                         v-if="tips==1"
                         min-width="15%"
                         prop="categoryCount"
                         align="center">
        </el-table-column>
        <el-table-column label="单据备注"
                         min-width="15%"
                         prop="inventoryRemark"
                         align="center">
        </el-table-column>
        <el-table-column label="操作"
                         fixed="right"
                         width="160"
                         align="center">
          <template slot-scope="scope">
            <el-button size="mini" type="warning" v-if="tips !=2" @click="check(scope.$index, scope.row)">
              查看
            </el-button>
            <el-button size="mini" type="danger" v-if="tips !=1" @click="delect(scope.row)">
              删除
            </el-button>
            <el-button size="mini" type="warning" v-if="tips !=1" @click="goOninven(scope.row)">
              继续盘点
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <el-card>
      <div class="column"></div>
    </el-card>
    <!-- 出库弹窗 -->
    <el-dialog title="添加商品"
               :visible.sync="dialogFormVisible"
               width="1220px"
               height="752px">
      <div>
        <!-- 筛选条件 -->
        <el-card class="filter-container"
                 shadow="never">
          <div>
            <i class="el-icon-search"></i>
            <span>筛选搜索</span>
            <el-button style="float: right"
                       @click="searchList"
                       type="primary"
                       size="small">
              查询
            </el-button>
            <el-button style="float: right;margin-right: 15px"
                       @click="reset"
                       size="small">
              重置
            </el-button>
          </div>
          <div style="margin-top: 15px">
            <el-form :inline="true"
                     :model="list"
                     size="small"
                     label-width="140px">
              <el-form-item label="商品名称：">
                <el-input style="width: 203px"
                          v-model="list.goodName"
                          clearable
                          placeholder="商品名称"></el-input>
              </el-form-item>
              <el-form-item label="商品编码：">
                <el-input style="width: 203px"
                          v-model="list.goodId"
                          clearable
                          placeholder="商品编码"></el-input>
              </el-form-item>
              <el-form-item label="规格编码：">
                <el-input style="width: 203px"
                          v-model="list.goodCode"
                          clearable
                          placeholder="规格编码："></el-input>
              </el-form-item>
            </el-form>
          </div>
        </el-card>

        <div class="table-container">
          <el-table :data="tableList"
                    style="width: 100%"
                    ref="multipleTable"
                    @selection-change="handleSelectionChange"
                    border>
            <el-table-column type="selection"
                             width="60"
                             align="center"></el-table-column>
            <el-table-column label="分类"
                             width="100"
                             align="center">
<template slot-scope="scope">
   {{scope.row.categoryName3}}
</template>
            </el-table-column>
            <el-table-column label="商品名称"
                             width="520"
                             align="center">
<template slot-scope="scope">
   {{scope.row.productName}}
</template>
            </el-table-column>
            <el-table-column label="商品编码"
                             width="200"
                             align="center">
<template slot-scope="scope">
   {{scope.row.productCode}}
</template>
            </el-table-column>
            <el-table-column label="规格编码"
                             width="200"
                             show-overflow-tooltip
                             prop="name"
                             align="center">
<template slot-scope="scope">
   {{scope.row.skuCode}}
</template>
            </el-table-column>
            <el-table-column label="库存总数"
                             width="00"
                             prop="sumStock"
                             align="center">
<template slot-scope="scope">
   {{scope.row.totalStockBefore}}
</template>
            </el-table-column>
          </el-table>
        </div>

        <!-- 固定在下面 -->
        <div class="fixedB">
          <div class="testButton">
            <el-button @click="result">取消</el-button>
            <el-button type="primary"
                       @click="addType">确定</el-button>
          </div>
          <!-- 页码 -->
        </div>

      </div>
    </el-dialog>

    <div class="fixedB open">
      <!-- 页码 -->
      <el-pagination background
                     style="margin-top: 10px;margin-left:20px"
                     @size-change="handleSizeChange"
                     @current-change="pageChange"
                     layout="total,sizes,prev,pager,next,jumper"
                     :page-size="page_size"
                     :page-sizes="[10, 30, 50, 100]"
                     :current-page="pageNum"
                     :total="total">
      </el-pagination>
    </div>

  </div>
</template>
<script>
  import {
    fetchList,
    stockChange
  } from '@/api/productInventory/productInventory'
  import {
    getPdList,
    getDraftList,
    getinventoryDraft,
    getDraftLists,
    delectDetails
  } from '@/api/productInventory/index'
  const defaultListQuery = {
    productName: '',
    orderNo: '',
    invenetTime: '',
    tips: '1',
  };
  export default {
    inject: ['reload'],
    data() {
      return {
        tips: '1',
        tableList: [],
        totalCount: 0,
        clock: false,
        open: false,
        inventoryCount: 0,
        inventoryCountLost: 0,
        textarea: '',
        input: '',
        num: '--',
        elseMoney: false, //一样
        successMoney: false,
        lostMoney: false,
        listQuery: Object.assign({}, defaultListQuery),
        list: {
          goodName: '',
          goodId: '',
          goodCode: ''
        },
        code: '',
        userName: '',
        timer: null,
        tableData: [],
        tableDataPre: [],
        pageNum: 1,
        page_size: 10,
        total: 0,
        totalAdd: 0,
        dialogFormVisible: false,
        updialogFormVisible: false,
        category: '',
        outStock: {
          number: '',
          status: '',
          remark: '',
          productId: '',
          skuId: ''
        },
        upStock: {
          number: '',
          status: '',
          remark: '',
          productId: '',
          skuId: ''
        }
      }
    },
    created() {
      this.getInventoryList()
    },
    methods: {
      delect(row){
        console.log(row)
        delectDetails({
          id:row.id
        }).then(res =>{
          console.log(res)
          if(res.code == 200){
            this.$message.success({
              type: 'success',
              message: res.message
            });
            this.getDraftList()
          }
        })
      },
      switchData(val) {
        console.log(val)
        this.listQuery.orderType = ''
        if (val == 1) {
          this.pageNum = 1
          this.getInventoryList();
        } else {
          this.getDraftList();
          this.pageNum = 1

        }
      },
      //临时保存
      tepreservation() {
        let datas = {
          inventoryNo: this.code, //盘点单编号
          shopId: localStorage.getItem("shopId"), //店铺ID
          bUserId: localStorage.getItem("userId"), //商户用户ID（盘点人）
          profitCount: this.inventoryCount, //盘盈总和
          lossCount: this.inventoryCountLost, //盘亏总和
          categoryCount: this.category, //商品分类总和
          productCount: this.totalCount, //盘点商品数量
          inventoryRemark: this.textarea, //单据备注
          list: this.tableData
        }
        getDraft(datas).then(res => {
          console.log(res)
          if (res.code == 200) {
            this.$message.success({
              type: 'success',
              message: res.message
            });
            this.reload()
          } else {
            this.$message.error({
              type: 'error',
              message: res.message
            });
          }
        })
      },
      //完成保存
      complete() {
        let datas = {
          inventoryNo: this.code, //盘点单编号
          shopId: localStorage.getItem("shopId"), //店铺ID
          bUserId: localStorage.getItem("userId"), //商户用户ID（盘点人）
          profitCount: this.inventoryCount, //盘盈总和
          lossCount: this.inventoryCountLost, //盘亏总和
          categoryCount: this.category, //商品分类总和
          productCount: this.totalCount, //盘点商品数量
          inventoryRemark: this.textarea, //单据备注
          list: this.tableData
        }
        getProAdd(datas).then(res => {
          console.log(res)
          if (res.code == 200) {
            this.$message.success({
              type: 'success',
              message: res.message
            });
            this.reload()
          } else {
            this.$message.error({
              type: 'error',
              message: res.message
            });
          }
        })
      },
      //添加商品
      addCom() {
        this.$router.push({
          path: '/inventoryManagement/inventory',
        })
      },
      handleSelectionChange(val) {
        console.log(val)
        this.multipleSelection = val;
        this.tableDataPre = val
        this.totalCount = this.tableData.length
      },
      ck(row) {
        // console.log(row)
        if (row.totalStockBefore > row.originalPriceAfter) {
          row.salePriceBefore = '盘盈'
        } else if (row.totalStockBefore < row.originalPriceAfter) {
          row.salePriceBefore = '盘亏'
        } else if (row.totalStockBefore = row.originalPriceAfter) {
          row.salePriceBefore = '无差异'
        }
        this.tableData.forEach(item => {
          this.tableData.push()
        })
        // console.log(this.tableData)
        this.inventoryCount = 0
        this.inventoryCountLost = 0
        for (var i = 0; i < this.tableData.length; i++) {
          console.log(this.tableData[i].salePriceBefore)
          if (this.tableData[i].totalStockBefore > this.tableData[i].originalPriceAfter) {
            this.inventoryCount++
          } else if (this.tableData[i].totalStockBefore < this.tableData[i].originalPriceAfter) {
            this.inventoryCountLost++
          } else {}
        }
        console.log(this.inventoryCount,this.inventoryCountLost)
      },
      addType() {
        this.tableData = this.tableDataPre
        this.tableData.forEach(item => {
          item.salePriceBefore = '--'
          item.remarks = ''
          item.originalPriceAfter = ''
        })
        let obj = {}; 
        const dataArry = this.tableData.reduce((preVal, curVal) => {  
          obj[curVal.categoryId3] ? '' : obj[curVal.categoryId3] = true && preVal.push(curVal);    
          return preVal 
        }, [])
        this.category = dataArry.length
        this.dialogFormVisible = false
        this.searchList()
      },
      result() {
        this.dialogFormVisible = false
        this.searchList()
      },
      handleSearchList() {
        this.listQuery.pageNum = 1;
        this.getInventoryList();
      },
      handleResetSearch() {
        this.listQuery = Object.assign({}, defaultListQuery);
        this.listQuery.pageNum = 1;
        if(this.tips ==1)
        {
          this.getInventoryList()
        }else{
          this.getDraftList();
        }
      },
      searchList() {
        getinventoryDraft({
          pageNum: this.pageNum,
          pageSize: this.page_size,
          productName: this.list.goodName,
          skuCode: this.list.skuCode,
          productCode: this.list.goodId,
        }).then(res => {
          this.tableList = res.data.list
          this.totalAdd = res.data.total
        })
      },
      getDraftList() {
        getDraftLists({
          shopId: localStorage.getItem("shopId"),
          pageSize: this.page_size,
          pageNum: this.pageNum,
          inventoryNo: this.listQuery.orderNo,
          createTime: this.listQuery.invenetTime,
        }).then(res => {
          console.log(res, '草稿列表')
          this.tableData = res.data.list
          this.total = res.data.total
        })
      },
      getInventoryList() {
        getPdList({
          shopId: localStorage.getItem("shopId"),
          pageSize: this.page_size,
          pageNum: this.pageNum,
          inventoryNo: this.listQuery.orderNo,
          createTime: this.listQuery.invenetTime,
        }).then(res => {
          console.log(res, '列表')
          this.tableData = res.data.list
          this.total = res.data.total
        })
      },
      replacef(name, id, row) {
        var obj = name.target;
        let _this = this
        if (obj.nodeName == 'SPAN') {
          //转换成输入框
          var input = document.createElement("input");
          input.className = "myStyle"
          input.value = obj.innerHTML;
          obj.parentNode.replaceChild(input, obj);
          //聚焦的替换后的input上
          input.focus();
          input.onblur = function() {
            obj.innerHTML = input.value;
            input.parentNode.replaceChild(obj, input);
            if (row.totalStockBefore > obj.innerHTML) {
              _this.successMoney = true //盘盈
              _this.lostMoney = false
              _this.elseMoney = false
            } else if (row.totalStockBefore < obj.innerHTML) {
              _this.lostMoney = true //盘亏
              _this.successMoney = false
              _this.elseMoney = false
            } else if (row.totalStockBefore = obj.innerHTML) {
              _this.elseMoney = true //一样
              _this.successMoney = false
              _this.lostMoney = false
            }
          };
        }
      },
      // 重置
      reset() {
        this.list = {
          goodName: '',
          goodId: '',
          goodCode: ''
        }
        if(this.tips ==1)
        {
          this.getInventoryList()
        }else{
          this.searchList()
        }
      },
      //修改每页页数
      handleSizeChange(page) {
        this.page_size = page
        if(this.tips ==1)
        {
          this.getInventoryList()
        }else{
          this.searchList()
        }
      },
      //点击跳转页码
      pageChange(page) {
        this.pageNum = page
          if(this.tips ==1)
        {
        this.getInventoryList()
        }else{
           this.getDraftList()
        }
      },
      outStockButton() {
        if (typeof(this.outStock.number) != "number") {
          this.$notify.error({
            title: '错误',
            message: '请输入数字'
          });
          return
        }
        if (this.outStock.number > this.outStock.total) {
          this.$notify.error({
            title: '错误',
            message: '出库量不能大于可用库存量'
          });
          return
        }
        if (this.outStock.status == '') {
          this.$notify.error({
            title: '错误',
            message: '请选择出库类型'
          });
          return
        }
        stockChange({
          shopId: localStorage.getItem("shopId"),
          productId: this.outStock.productId,
          skuId: this.outStock.skuId,
          type: this.outStock.status,
          totalStockChange: -this.outStock.number,
          remark: this.outStock.remark,
          creater: localStorage.getItem("userId")
        }).then(res => {
          if (res.code == 200) {
            this.$notify({
              title: '成功',
              message: '出库成功',
              type: "success"
            });
            this.dialogFormVisible = false
            this.outStock.status = ''
            this.outStock.skuId = ''
            this.outStock.number = ''
            this.outStock.remark = ''
            this.searchList()
          } else {
            this.$notify.error({
              title: '失败',
              message: '出库失败',
            });
          }
        })
      },
      // 查看
      check(index, row) {
        this.$router.push({
          path: '/inventoryManagement/inventoryList/inventoryDetail',
          query: {
            id: row.id
          }
        })
      },
      goOninven(row) {
        console.log(row)
        this.$router.push({
          path: '/inventoryManagement/inventory',
          query: {
            id: row.id
          }
        })
      },
      upStockButton() {
        if (typeof(this.upStock.number) != "number") {
          this.$notify.error({
            title: '错误',
            message: '请输入数字'
          });
          return
        }
        if (this.upStock.status == '') {
          this.$notify.error({
            title: '错误',
            message: '请选择入库类型'
          });
          return
        }
        stockChange({
          shopId: localStorage.getItem("shopId"),
          productId: this.upStock.productId,
          skuId: this.upStock.skuId,
          type: this.upStock.status,
          totalStockChange: this.upStock.number,
          remark: this.upStock.remark,
          creater: localStorage.getItem("userId")
        }).then(res => {
          if (res.code == 200) {
            this.$notify({
              title: '成功',
              message: '入库成功',
              type: "success"
            });
            this.updialogFormVisible = false
            this.upStock.status = ''
            this.upStock.skuId = ''
            this.upStock.number = ''
            this.upStock.remark = ''
            this.searchList()
          } else {
            this.$notify.error({
              title: '失败',
              message: '入库失败',
            });
          }
        })
      }
    }
  }
</script>
<style lang="scss" scoped>
  .app-container {
    padding: 20px 20px 40px 20px;
  }
  .tips {
    color: green;
    margin-left: 20px;
    font-size: 14px;
  }
  .testButton {
    float: left;
    margin-top: 20px;
  }
  .myStyle {
    width: 100px;
    height: 35px;
    padding-left: 10px;
  }
</style>
<style>
  .myStyle {
    width: 100px;
    height: 35px;
    padding-left: 10px;
  }
  .total {
    width: 100%;
    border: 1px solid #eee;
    height: 50px;
    line-height: 50px;
    padding: 0 10px;
  }
  .totaltextarea {
    width: 100%;
    border: 1px solid #eee;
    height: 50px;
    line-height: 50px;
    margin-top: 10px;
  }
  .total>span {
    margin-right: 45px;
  }
</style>

